.project {
  position: relative;
  width: 100%;
  max-width: 500px;
  height: 300px;
  margin: 30px;
}

.project-bg {
  position: relative;
  width: 100%;
  height: 100%;
}

.project-text {
    color: var(--primary);
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    padding: 50px;
    z-index: 900;
    opacity: 0;
    transition: all .5s ease-in-out;
}

.project-text:hover {
  background-color: var(--hide);
  opacity: 1;
  
}

.project:hover {
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.8);
}

.project-text h3 {
  font-size: 2.5rem;
}

.project-text a {
  text-decoration: none;
  font-weight: bold;
  color: var(--primary);
  transition: all .3s ease-in-out;
}

.project-text a:hover {
  color: var(--secondary);
}

.resume a {
  font-size: 2rem;
  color: var(--primary);
}

.resume a:hover {
  text-shadow: 2px 2px 10px rgba(0,0,0, 0.25);
  color: var(--secondary);
}

button {
	background: var(--primary);
	border: 0;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
	font-size: 16px;
	font-weight: 600;
	margin-bottom: 30px;
	padding: 10px 15px;	
}

a {
	border-radius: 5px;
	color: var(--secondary);
	font-weight: 600;
	padding: 2px 4px;
	margin-left: -2px;
	margin-right: -2px;
	
}

.navActive {
  color: var(--secondary);
  text-shadow: 2px 2px 10px rgba(0,0,0, 0.25);
}